.profile-wrap {
    width: 100%;
    display: flex;
    align-items: center;

    &::after {
        position: absolute;
        top:82px;
        z-index: 100;
        left: 0;
        width: 100%;
        display: block;
        content: " ";
        background-color: transparent;
        border-bottom: 1px solid rgb(37, 51, 65);
        // box-shadow: 0px 2px 3px -1px rgba(0,0,0,0.2), 0px 3px 3px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
    }
}

.profile-img {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    position: relative;
    background-color: #ffffff;
    padding: 5px;
    flex-shrink: 0;

    img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        object-fit: 100%;
    }
}

.profile-details {
    margin-left: 15px;
    color: #ffffff;

    .name {
        display: inline-block;
        font-size: 19px;
        font-weight: 500;
        margin: 0;
        line-height: 1.2;

        &::after {
            display: block;
            content: " ";
            width: 220px;
            height: 1px;
            margin: 1px 0;
            background-color: #3d566e;
        }
    }

    .desc {
        margin: 0;
        line-height: 1.2;
        font-size: 13px;
    }
}