.about-wrap {
    color: #ffffff;

    .item {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 40px;
    }

    .text-wrap {
        margin-left: 15px;
        line-height: 1.2;
        align-self: center;
        max-width: 250px;
    }

    .text {
        display: block;
        font-size: 15px;
        line-height: 1.2;

        &::after {
            display: block;
            content: "";
            width: 100%;
            height: 1px;
            background-color: #3d566e;
            margin: 1px 0;
        }
    }

    .text-desc {
        font-weight: 100;
        font-size: 13px;
        font-weight: 100;
        line-height: 1.2;
        margin-top: 2px;
    }

    .icon-wrap {
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        width: 100px;
        height: 100px;
        background-color: transparent;
        box-shadow: 0px 2px 3px -1px rgba(0,0,0,0.2), 0px 3px 3px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
    }

    .icon {
        font-size: 52px;
        color: #ff4500;
    }
}
