.connect-container {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 25px;
.small-title {
    font-weight: 400;
    &::after {
        top:82px;
        z-index: 100;
        left: 0;
        width: 100%;
        display: block;
        content: " ";
        background-color: transparent;
        border-bottom: 1px solid rgb(37, 51, 65);
    }
}

.item-wrap {
    padding: 5px 10px;
    margin: 15px 0;
    min-width: 138px;
    border-radius: 3px;
    background-color: #ffffff;
    box-shadow: 0px 2px 3px -1px rgba(0, 0, 0, 0.2), 0px 3px 3px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);

    &:last-of-type {
        margin-left: 15px;
    }
}

.icon-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5px;

    .github {
        font-size: 30px;
        color: black;
    }
    .youtube {
        font-size: 42px;
        color: red;
        margin-left: 10px;
    }
    .linkedin {
        font-size: 35px;
        color: #0e76a8;
    }
    .twitter {
        font-size: 35px;
        margin-left: 10px;
        color: #00acee;
    }
}
    a {
        text-decoration: none;
        outline: none;
    }

}

.main-title {
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
    text-align: center;
}